img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  display: block;
}

*, *::before, *::after {
  box-sizing: border-box;
}

ul, li, h1, h2, h3, h4, h5, h6, p {
  margin: 0px 0px;
  padding: 0px 0px;
}

.row {
  margin: 0px 0px;
  padding: 0px 0px;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0px 15px;
}

.text-hidden {
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden;
}

.h-100 {
  height: 100%;
}

